.footer-container {
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  padding: 1.5rem 0;
  color: white;
}

.footer-container-column {
  display: flex;
  justify-content: space-around;
  padding: 1rem 0;
}

.footer-container p {
  text-align: center;
  font-size: 15px;
  padding: 2rem 2rem 0 2rem;
}

.footer-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
}

.footer-column:first-child {
  width: 10%;
}

.footer-column a {
  text-align: center;
  align-items: center;
  font-size: 1.2rem;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.footer-column a:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  text-decoration: underline;
}

.footer-column h3 {
  padding: 1rem;
  text-align: center;
}

hr {
  border: none;
  border-left: 1px solid hsla(200, 10%, 50%, 100);
  height: 25vh;
  width: 1px;
}

.footer-column img {
  height: 8rem;
  margin: 2rem 0 0 2rem;
}

