* {
  margin: 0;
}

.quiz-start {
  height: 82vh;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.2)), url(/DobbyAndFriends/static/media/quiz-bg.928f99f0.jpg);
  background-size: cover;
  background-color: #fbeee6;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 3%;
}

.quiz-rules {
  width: 100%;
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7vw;
  background-color: cornsilk;
  margin-bottom: 5vh;
}

#start {
  background-color: black;
  color: white;
  font-size: 3vw;
  height: 16vw;
  width: 16vw;
  border-radius: 8vw;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

#start:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: white;
  color: black;
}

.img-question-answers {
  width: 80%;
  height: 80%;
  display: flex;
  align-items: space-around;
  justify-content: space-around;
}

.question p {
  text-align: center;
  display: inline-block;
  font-size: 2vw;
  margin-bottom: 2vw;
  font-weight: bold;
}

.img img {
  background-color: black;
  padding: 0.7vw;
  height: 24vw;
  width: 16vw;
  margin-top: 2vw;
}

.question-answers {
  height: 80%;
  width: 70%;
  margin-top: 5vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.answers {
  width: 100%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 5%;
  padding: 0;
}

.answer {
  width: 45%;
  height: 30%;
  margin: 0.2vw;
  background-color: blanchedalmond;
  border-radius: 2vw;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: -webkit-transform 0.3s background-color 1s;
  transition: transform 0.3s background-color 1s;
  transition: transform 0.3s background-color 1s, -webkit-transform 0.3s background-color 1s;
  position: relative;
  font-size: 1.4vw;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
}

.answer img {
  height: 18vh;
  display: none;
  position: relative;
  left: 20%;
}

.answer:hover img {
  display: block;
}

.answer:hover {
  border: solid 2px black;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.true-answer {
  background-color: darkgreen;
  color: floralwhite;
}

.wrong-answer {
  background-color: darkred;
  color: floralwhite;
}

