#message {
  height: 50px;
}

.contact-container {
  height: 82vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  background-attachment: fixed;
  background-image: url(/DobbyAndFriends/static/media/pattern.b024d801.jpg);
  padding: 1rem;
}

.image img {
  width: 25vw;
}

.image {
  display: flex;
  align-items: center;
}

.form-container {
  padding: 3%;
  border-radius: 10px;
  margin: 1% 8% 8% 8%;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.form-contact {
  display: flex;
  flex-direction: column;
  align-items: wrap;
  margin: 2% 0;
}

input[type=text],
textarea,
input[type=email] {
  width: 30vw;
  height: 5vh;
  padding: 12px;
  margin: 1% 3%;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
}

h3 {
  margin: 1% 3%;
}

.btn-contact {
  padding: 10px 0;
  width: 30%;
  font-size: 1.1rem;
  border-radius: 10px;
  background-color: white;
  color: black;
  font-weight: bold;
  border: black solid 2px;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  margin-top: 1rem;
}

#submit:hover {
  background-color: rgb(110, 110, 110);
}

.image-form {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.off {
  display: none;
}

.active {
  text-align: center;
}

@media screen and (max-width: 720px) {
  .title-contact {
    font-size: 3vw;
  }

  .h3-contact {
    font-size: 2vw;
  }

  .image img {
    width: 30vw;
  }

  input[type=text],
  textarea,
  input[type=email] {
    width: 30vw;
    height: 3vh;
    padding: 12px;
    margin: 1% 3%;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
  }

  .btn-contact {
    width: 15vw;
  }
}

